.default-view .recoginitions-list {
  padding-left: 25px;
}

.default-view .recoginitions-list li {
  line-height: 22px;
  margin-bottom: 15px;
}

.default-view .recoginitions-list li:last-child {
  margin-bottom: 0;
}

.recoginitions-list li div {
  margin-bottom: 0;
  margin-left: 25px;
}

.recoginitions-list li i {
  font-size: 10px;
  margin-right: 10px;
}

.toggle-view-btns span:last-child {
  background: var(--secondary-color);
  color: var(--white);
}

.rubixs-view .toggle-view-btns span:first-child {
  background: var(--secondary-color);
  color: var(--white);
}

.rubixs-view .toggle-view-btns span:last-child {
  background: var(--white);
  color: var(--black);
}


/* rubixs cube view */
.recoginitions-list {
  padding-left: 25px;
}

.recoginitions-wrapper.rubixs-view h3 {
  margin-bottom: 45px;
}

.rubixs-view .recoginitions-list {
  margin: auto;
  transform-style: preserve-3d;
  animation: rubixsAnimation 25s infinite linear;
  width: 200px;
  height: 280px;
  position: relative;
  overflow: visible;
}

.rubixs-view .cube-wrapper {
  perspective: 800px;
  perspective-origin: 40% 90px;
  transform: rotate(-30deg);
}

.rubixs-view .recoginitions-list li {
  position: absolute;
  line-height: 22px;
  margin: 10px 0 0;
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: var(--white);
}

.rubixs-view .recoginitions-list li>* {
  margin-left: 15px;
}

.rubixs-view .recoginitions-list li>p {
  text-align: center;
  margin-bottom: 15px;
}

.rubixs-view .recoginitions-list li>p i {
  display: none;
}

.rubixs-view .recoginitions-list li:first-child {
  background: var(--primary-color);
  transform: translateZ(110px);
}

.rubixs-view .recoginitions-list li:nth-child(2) {
  background: var(--secondary-color);
  transform: rotateY(270deg) translateX(-90px);
  transform-origin: center left;
}

.rubixs-view .recoginitions-list li:nth-child(3) {
  transform: rotateY(-270deg) translateX(90px);
  transform-origin: top right;
  background: var(--custom-blue);
}

@keyframes rubixsAnimation {
  0% {
    transform: rotateY(0deg);
  }

  25% {
    transform: rotateY(88deg);
  }

  50% {
    transform: rotateY(0deg);
  }

  75% {
    transform: rotateY(-90deg);
  }

  100% {
    transform: rotateY(0deg);
  }
}

@media screen and (max-width:768px) {
  .rubixs-view .cube-wrapper {
    margin-top: 80px;
  }
}